import { Paper } from "@mui/material";
import { useState } from "react";
import AdminPage from "../AdminPage";
import Content from "../Content";

export default function ImagesComp(){
    const [currentComp, setCurrentComp] = useState();
    return(
    <Paper elevation={0} style={{textAlign:"center",marginTop:"5%"}}>
    {currentComp && <AdminPage modify={true} currentComp={currentComp} setCurrentComp={setCurrentComp}></AdminPage>}
    
    {!currentComp && 
        <div>
        <h2>Select Album</h2>
        <Content  currentPageSetter={()=>{}} setSelectedAlbum={(v)=>{setCurrentComp(v)}}  ></Content>
        </div>
    }
   
    </Paper>);
}